import returns from '../../common/returns.vue'
export default{
	components:{
		returns
	},
	data() {
		return {
			title:'退款/退货申请',
			show:0,
			add_show:1,
			type:1,
			data:'',
			goods_data:'',
			order_data:'',
			return_only:['请选择'],
			return_money:['请选择'],
			index:0,
			image_list:[],
			images:[],
			num:'',
			price:'',
			reason:'',
			msg:''
		}
	},
	methods:{
		bindPickerChange: function(e) {
			this.index = e.target.value
			this.reason= e.target.value
		},
		chiose(show){
			this.show = show
			this.index = 0
		},
		change_num(type){
			if(this.type == 2) return
			if(type == 1){
				this.num == 1? this.num = 1 : this.num --
				
			}else{
				this.num == this.data.returned_data.returned_quantity?this.num = this.data.returned_data.returned_quantity :this.num ++
			}
		},
		add_image(){
			let that = this
			uni.chooseImage({
			    count: 1, //默认9
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
					let times = that.service.loading('上传中')
					uni.uploadFile({
					    url: that.APIconfig.api_root.subuser.threeuser.s_ueditor_index,
					    filePath: res.tempFilePaths[0],
					    name: 'upfile',
					    formData: {
					      user_id: that.$store.state.user.id,
						  action:'uploadimage',
						  path_type:that.data.editor_path_type,
						  token:that.$store.state.token
					    },
					    success: (ref) => {
							uni.hideLoading()
							clearTimeout(times)
							
					        if(JSON.parse(ref.data).code == 0){
								console.log(JSON.parse(ref.data))
								 that.image_list.push(res.tempFilePaths[0])
								 that.images.push(JSON.parse(ref.data).data.url)
								 if(that.image_list.length == 3)that.add_show = 2
							}
					    }
					});
			    }
			});
		},
		deletes(index){
			this.image_list.splice(index,1)	
			this.images.splice(index,1)
			if(this.image_list.length < 3)this.add_show = 1
		},
		ascertain(){ //提交
			let data = new Object()
			if(this.show == 1) data.number = this.num
			data.price = this.price
			data.images = this.images
			data.type = this.show
			data.user_id = this.$store.state.user.id
			if(this.show == 0)data.reason = this.return_only[this.reason] 
			if(this.show == 1)data.reason = this.return_money[this.reason] 
			if(data.reason =='请选择' || this.price =='' || this.msg.length < 5 || this.images.length == 0){
				uni.showToast({
					icon:'none',
					title:'请完整填写退款信息'
				})
				return
			}
			data.msg = this.msg
			let arr_value = Object.values(data)
			if(arr_value.indexOf(undefined) != -1){
				uni.showToast({
					icon:'none',
					title:'请完整填写退款信息'
				})
				return
			}
			data.order_id = this.oid
			data.order_detail_id = this.id
			let url
			this.type == 1? url = this.APIconfig.api_root.subuser.threeuser.s_aftersale_create :url = this.APIconfig.api_root.subuser.threeuser.g_Create
			this.service.entire(this,'post',url,data,function(self,res){
				uni.showToast({
					icon:'none',
					title:res.msg
				})
				if(res.code == 0){
					setTimeout(function(){
						uni.navigateBack({
						    delta: 2
						});
					},1500)
				}
			})
			
		}
	},
	
	onLoad(e) {
		console.log(e)
		if(e.id){  //商城订单
			this.oid = e.oid
			this.id = e.id
			this.service.entire(this,'post',this.APIconfig.api_root.subuser.threeuser.s_order_aftersale,{
				oid:e.oid,
				did:e.id,
				user_id: this.$store.state.user.id,
			},function(self,res){
				self.data = res.data
				self.goods_data = res.data.order_data
				self.num = res.data.returned_data.returned_quantity
				self.order_data = res.data.order_data
				self.price = res.data.returned_data.refund_price
				self.return_only.push(...res.data.return_only_money_reason)
				self.return_money.push(...res.data.return_money_goods_reason)
			})
		}else{//拼团订单
			this.type = 2
			this.oid = e.oid
			this.service.entire(this,'get',this.APIconfig.api_root.subuser.threeuser.g_Aftersale,{
				oid:e.oid,
				user_id: this.$store.state.user.id,
			},function(self,res){
				self.data = res.data
				self.goods_data = res.data.goods[0]
				console.log(self.goods_data)
				self.num = 1
				self.order_data = res.data.order.order
				console.log(self.order_data)
				self.price = res.data.order.order.price
				self.return_only.push(...res.data.return_only_money_reason)
				self.return_money.push(...res.data.return_money_goods_reason_list)
			})
		}
		
	}
}
